#form input{
  background-color: #8FFFF9;
  border: 0; 
  color: black;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 5px;
  font-family: 'Permanent Marker', cursive;
}
#form input::-webkit-input-placeholder{
  color: black
}
#form input::-moz-placeholder{
  color: black
}
#form input:-ms-input-placeholder{
  color: black
}
#form input::-ms-input-placeholder{
  color: black
}
#form input::placeholder{
  color: black
}
#form input:focus{
  outline: none;
}
#form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color:#FFB5F2; 
  cursor: pointer;
}
#form button:focus{
  outline: none;

  
}


.list{
    color: black;
    width: 300px;
    margin: 20px auto;
    height: 50px;
    background-color: #FFE982;
    border-radius: 5px;
}
.list p{
    padding: 10px;
    position: relative;
    
}
.list p input{
    background-color: transparent;
    border: 0;
    color:  black;
    font-size: 18px;
    font-family: 'Permanent Marker', cursive;
}
.list p input:focus{
    outline: none;
}
.list p span{
    position: absolute;
    right: 10px;
}
.faicons{
    margin: 0 5px;
    font-size: 50px;
    cursor: pointer;
}



